import Vue from 'vue';
import Router from 'vue-router';
const _import = require('./_import_' + process.env.NODE_ENV);
// in development env not use Lazy Loading,because Lazy Loading large page will cause webpack hot update too slow.so only in production use Lazy Loading

/* layout */
import Layout from '../views/layout/Layout';
/*const Layout = resolve =>{
    require(['../views/layout/Layout.vue'],resolve);
};*/
/* login */
const Login = _import('login/index');
/*const Login = resolve =>{
    require(['../views/login/index.vue'],resolve);
};*/
/* dashboard */
const dashboard = _import('dashboard/index');
/*const dashboard = resolve =>{
    require(['../views/dashboard/index.vue'],resolve);
};*/
/* error page */
const Err404 = _import('404');

/* demo page */
const Form = _import('page/form');

//
const News = _import('news/index');
const SendNews = _import('news/mnews/SendNews');
const EditNews = _import('news/mnews/editnews');


//
const TieZi = _import('tiezi/index');
const sendtiezi = _import('tiezi/mtiezi/sendtiezi');
const EditTieZi = _import('tiezi/mtiezi/edittiezi');
//
const Datum = _import('datum/index');
const upload = _import('datum/mdatum/upload');
const download = _import('datum/mdatum/download');

//()
const InnderUser = _import('inneruser/index');
const AddUser = _import('inneruser/muser/adduser');
const EditUser = _import('inneruser/muser/edituser');

//
const AdvHome = _import('adv/index');
const AddAdv = _import('adv/madv/addadv');
const EditAdv = _import('adv/madv/editadv');

//
const CommentHome = _import('comment/index');
const ActiveU = _import('comment/activeu');

//
const PermissionHome = _import('permanage/index');

//
const TagHome = _import('tag/index');
const addTag = _import('tag/mtag/addtag');
const editTag = _import('tag/mtag/edittag');
const addCategory = _import('tag/mtag/addcategory');
const editCategory = _import('tag/mtag/editcategory');
const categoryIndex = _import('tag/mtag/category');

//
const JiFenHome = _import('jifen/index');
const addJiFen = _import('jifen/mjifen/addjifen');
const editJiFen = _import('jifen/mjifen/editjifen');

//
const Table = _import('table/index');
Vue.use(Router);

 /**
  * icon : the icon show in the sidebar
  * hidden : if `hidden:true` will not show in the sidebar
  * redirect : if `redirect:noredirect` will not redirct in the levelbar
  * noDropdown : if `noDropdown:true` will not has submenu in the sidebar
  * meta : `{ role: ['admin'] }`  will control the page role
  * //
  //
  **/
export const constantRouterMap = [
  { path: '/login', component: Login, hidden: true },
  { path: '/404', component: Err404, hidden: true },
  {
    path: '/',
    component: Layout,
    redirect: '/dashboard',
    name: 'Home',
    hidden: true,
    children: [{ path: 'dashboard', component: dashboard }],
  }
];

export default new Router({
  mode: 'history',
  base: '/webapp/admin/',//
  scrollBehavior: () => ({ y: 0 }),
  routes: constantRouterMap
});
//
//
export const asyncRouterMap = [
  {
    path: '/home',
    component: Layout,
    redirect: '/home/index',
    name: '',
    icon: 'zujian',
    noDropdown: true,
    children: [
      { path: 'index', component: Form,icon: 'zonghe',name: '' }
    ]
  },
    {
        path: '/table',
        component: Layout,
        redirect: '/table/index',
        name: '',
        noDropdown: true,
        icon: 'tubiaoleixingzhengchang',
        //, meta: { role: ['admin',''] }
        children: [
            { path: 'index', component: Table, name: '' },
        ]
    },

    {
        path:'/news',
        component:Layout,
        redirect:'news/mnews/SendNews',
        name: '',
        icon: 'zujian',
        children: [//
            { path: 'index', component: News, name: '', icon: 'zonghe' },
            { path: 'SendNews', component: SendNews, name: '', icon: 'zonghe' },
            { path: 'editnews/:newsId', component: EditNews, name: '', icon: 'zonghe',meta: {
                title: '',
                needAuth: true
            }   }
        ]
  },
    {
        path:'/tiezi',
        component:Layout,
        redirect:'tiezi/mtiezi/sendtiezi',
        name: '',
        icon: 'zujian',
        children: [//EditTieZi
            { path: 'index', component: TieZi, name: '', icon: 'zonghe' },
            { path: 'sendtiezi', component: sendtiezi, name: '', icon: 'zonghe' },
            { path: 'edittiezi/:tieId', component: EditTieZi, name: '', icon: 'zonghe',meta: {
                title: '',
                needAuth: true
            }   }
        ]
  },
    {
        path:'/datum',
        component:Layout,
        redirect:'datum/mdatum/upload',
        name: '',
        icon: 'zujian',
        children: [ //
            { path: 'index', component: Datum, name: '', icon: 'zonghe' },
            { path: 'upload', component: upload, name: '', icon: 'zonghe' },
            { path: 'download', component: download, name: '', icon: 'zonghe' }
        ]
  },
    {
        path:'/comment',
        component:Layout,
        redirect:'comment/index',
        name: '',
        icon: 'zujian',
        children: [ //
            { path: 'index', component: CommentHome, name: '', icon: 'zonghe' },
            { path: 'activeu', component: ActiveU, name: '', icon: 'zonghe' },
        ]
    },
    {
        path:'/jifen',
        component:Layout,
        redirect:'jifen/index',
        name: '',
        icon: 'zujian',
        children: [ //
            { path: 'index', component: JiFenHome, name: '', icon: 'zonghe' },
            { path: 'addjifen', component: addJiFen, name: '', icon: 'zonghe' },
            { path: 'editjifen/:jId', component: editJiFen, name: '', icon: 'zonghe' },

        ]
    },
    {
        path:'/tag',
        component:Layout,
        redirect:'tag/mtag/addtag',
        name: '',
        icon: 'zujian',
        children: [ //
            { path: 'index', component: TagHome, name: '', icon: 'zonghe' },
            { path: 'addtag', component: addTag, name: '', icon: 'zonghe' },
            { path: 'edittag/:tagId', component: editTag, name: '', icon: 'zonghe' },
            { path: 'catgeory', component: categoryIndex, name: '', icon: 'zonghe' },
            { path: 'addcategory', component: addCategory, name: '', icon: 'zonghe' },
            { path: 'editcategory/:catId', component: editCategory, name: '', icon: 'zonghe' },
        ]
    },
    {
        path: '/inneruser',
        component: Layout,
        redirect: 'inneruser/muser/adduser',
        name: '',
        icon: 'tubiaoleixingzhengchang',
        //EditUser
    // , meta: { role: ['admin',''] }
children: [
            { path: 'index', component: InnderUser, name: ''},
            { path: 'adduser', component: AddUser, name: ''},
            { path: 'edituser/:adminId', component: EditUser, name: '', icon: 'zonghe',meta: {
                title: '',
                needAuth: true
            }   }
            ]

    },
    {
        path: '/adv',
        component: Layout,
        redirect: 'adv/madv/addadv',
        name: '',
        icon: 'tubiaoleixingzhengchang',
        //
        children: [
            { path: 'index', component: AdvHome, name: '' },
            { path: 'addadv', component: AddAdv, name: '' },
            { path: 'editadv/:advId', component: EditAdv, name: '' }
        ]

    },
  {
    path: '/permanage',
    component: Layout,
    redirect: '/permanage/index',
    name: '',
    icon: 'tubiaoleixingzhengchang',
    noDropdown: true,//
    //
    children: [
        { path: 'index', component: PermissionHome, name: '', meta: { role: ['admin',''] } }
        ]
  },

  { path: '*', redirect: '/404', hidden: true }
];



