import fetch from 'axios';
// import fetch from '../utils/fetch';
/**
 * 
 * @param mobile:
 * @param password:
 * @param role_name:(2)
 */
export function login(mobile, password,role_name) {
  return fetch({
    url: '/api-token-auth/',
    // url: '/user/login',
    method: 'post',
    data: {
        mobile,
        password,
        role_name
    }
  });
}

