// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
import ElementUI from 'element-ui'
import { Message } from 'element-ui';
import 'element-ui/lib/theme-default/index.css'
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import 'normalize.css/normalize.css'
import '@/assets/iconfont/iconfont'
import IconSvg from '@/components/Icon-svg/index.vue'
import { getToken } from '@/utils/auth'
import axios from 'axios';
import $ from 'jquery';
import 'bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap/dist/js/bootstrap.min.js'
import Font from 'font-awesome-webpack';
Vue.config.productionTip = false

Vue.use(ElementUI);
Vue.component('icon-svg', IconSvg)
//
const whiteList = ['/login'];
// http request 
axios.interceptors.request.use(
    config => {
        //token
        if (getToken()) {
            config.headers.Authorization = ` JWT ${getToken()}`;
        }
        return config;
    },
    err => {
        return Promise.reject(err);
    }
);
axios.interceptors.response.use(
    response => {
        return response;
    },
    error => {
        return Promise.reject(error);
    });

axios.defaults.baseURL = 'http://www.ecmagnet.com';
// axios.defaults.baseURL = 'http://192.168.0.200:8080';
axios.defaults.headers['Content-Type'] = 'application/json;charset=utf-8';
// 
router.beforeEach((to, from, next) => {
    NProgress.start();
    if (getToken()) { //
        if (to.path === '/login') {
            next({ path: '/' }); //
        } else {
            //user_info
            //
            if (store.getters.roles.length === 0) {
                //()
                store.dispatch('GetInfo').then(res => {
                    let roles = [];
                    if(res.data.code === 200){
                        roles = res.data.data.detail.roles;
                    }
                    const len = roles.length;
                    // console.log("roles:",res);
                    if(len===0){
                        Message({
                            message: "",
                            type: 'error',
                            duration: 5 * 1000
                        });
                        return;
                    }
                    let rolesStr = '';
                    for(let i = 0;i<len;i++){
                        rolesStr+=roles[i].name+',';
                    }
                    const ros = rolesStr.split(',');
                    store.dispatch('GenerateRoutes', { ros }).then(() => {
                        //
                        router.addRoutes(store.getters.addRouters);
                        next({ ...to });
                    })
                })
            } else {
                next();
            }
        }
    } else {
        //,
        if (whiteList.indexOf(to.path) !== -1) {
            next()
        } else {
            next('/login');
            NProgress.done();
        }
    }
});
//axiosVue
// Vue.prototype.$http = axios;
router.afterEach(() => {
    NProgress.done();
});

new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App }
})



